import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link, match } from 'react-router-dom';
import "../assets/blog.css";

const BlogDetail = (props) => {
    const [blog, setBlog] = useState({});

    const slug = props.match.params.id;
    console.log(slug)

    const fetchData = async () =>{
        const url = 'http://localhost:8000/api/blog/' + slug;
        const config = {
            headers:{
                'Content-Type':'application/json',
            }
        }
        try{
            const res = await axios.get(url, config);
            console.log(res.data[0]);
            setBlog(res.data[0]);
        }
        catch(err){
            console.log(err);
        }
        
    };
        

    useEffect(()=>{
        fetchData();
        console.log(blog)
        return;
      },[])

    const createBlog = () => {
        return{__html:blog.content};
    };
    console.log(createBlog());

    if( blog.slug === slug ){
        return(
        
            <>
            <div style={{alignContent:"center", position:"relative"}}>
                <div className="container mt-5 bg-white" style={{backgroundImage:"https://images.unsplash.com/photo-1503334849647-1d48ae0ba696?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80", 
                borderStyle:"double", 
                borderRadius:"25px",
                backgroundSize: "100%",
                alignItems:"center"}}>
                    <h1 className="display-2" style={{ marginRight:"10px", color: '#2182b380', border:'none', padding:'0px'}}>{blog.title}</h1>
                    <img style={{height: '50%', width:'50%', alignItems:"center"}} src={blog.thumbnail} alt={blog.title} />
                    <h2 className="text-capitalize" style={{color:"darkgreen"}}>{blog.category}</h2>
                    <h6 className="display-4">{blog.excerpt}</h6>
                    <div>
                    <article>{blog.content}</article>
                    {/* <div className="mb-5 mt-5" dangerouslySetInnerHTML={createBlog()}  /> */}
                    </div>
                    <div>
                    <medium className="text-muted text-capitalize">{blog.month}, {blog.day}</medium>
                    </div>
                    <div>
                    <medium className="text-capitalize" style={{color:"darkgreen"}}><Link to='/api/blogs'></Link>Back to Blogs</medium>
                    </div>
                    </div>    
            </div>
            </>
            
            )
    

    }
    return (
        <div>
            <h1 className="display-2" style={{color:"darkgreen"}}>You failed</h1>
        </div>
    );


    // createBlog(fetchData());
};

export default BlogDetail;