import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import '../assets/blog.css';
import axios from 'axios';

const Blog = () => {

    const [posts, setPosts] = useState([]);

    const getPosts = async()=>{
        const url = 'http://localhost:8000/api/blog/bloglist/'
        const config = {
            headers:{
                'Content-Type':'application/json',
            }
        }
        try{
            const res = await axios.get(url,config);
            console.log(res.data)
            const postList = setPosts(res.data);
            if(postList){
                return(200);
            }

        }catch(err){
            console.log(err);
        }
    }
    useEffect(()=>{
        getPosts();
        console.log(posts)
        return;
      },[])
    
    return(
    <div className="container mt-5">
        <div className="nav-scroller py-1 mb-2">
            <nav className="nav d-flex justify-content-between">
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/world">WORLD</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/environment">ENVIRONMENT</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/trees">TREES</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/climate">CLIMATE</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/surveys">SURVEYS</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/drives">DRIVES</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/forestation">FORESTATION</Link>
                <Link className="p-2 text-muted" style={{color:"white"}} to="/categories/diy">DIY</Link>
            </nav>
        </div>

        <div className="row my-5">
        {
            posts.length > 0 ?
            posts.map((post,id) => (
                
                <>
                <div key={id} className="col-sm-12 col-md-4 col-lg-4 mt-3" >
                    <div className="card bg-white shadow-lg p-3" style={{borderRadius:'10px', position:'relative'}}>
                        {   
                            post.featured ?
                            <div style={{position: 'absolute', right:'0px'}}>
                                <i className="fas fa-2x hvr-grow fa-star text-warning"/>
                            </div>  : ''
                        }
                        
                        <img className="card-img-top" style={{height: '200px', width:'100%'}} src={post.thumbnail} alt={post.title} />
                        <h5 className="card-header" style={{color: 'darkgreen', backgroundColor:'transparent', border:'none', padding:'0px'}}>{post.title}</h5>
                        <small className="text-muted text-capitalize">{post.month}, {post.day}</small>
                        <br/>
                        {/* <small className="w-20 bg-white text-capitalize p-3 text-center border border-success" style={{borderRadius:"17px"}}>{post.category}</small>
                        <br/> */}
                        <small className="text-capitalize" style={{color:"darkgreen"}}>{post.category}</small>
                        <br/>
                        <div className="card-body p-1" style={{maxHeight:'100px', minHeight:'100px', overflowX:'hidden', overflowY:'scroll'}}>
                            <h6>{post.excerpt}</h6>
                            <article>{post.content}</article>
                        </div>
                        <Link to={`blog/${post.slug}`} className="stretched-link">Continue reading</Link>
                    </div>
                </div>
                </>
     
            )):(
                <h1>LOADING</h1>
            )
        }
        </div>
    </div>

    );
};


export default Blog;