import React, { Fragment } from 'react';
import { Link, NavLink } from 'react-router-dom';
import '../assets/navbar.css';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';

const Navbar = ({ logout, isAuthenticated }) => {
    const guestLinks = () => (
        <Fragment>
            <li className="nav-item">
                <Link className="nav-link hvr-glow" to="/login">Login/Signup</Link>
            </li>
        </Fragment>
    );

    const authLinks = () => (
        <Fragment>
            <li className="nav-item">
                <a className="nav-link hvr-glow" href="#!" onClick={logout}>Logout</a>
            </li>
        </Fragment>
    );


return (
    <>
    <span></span>
    <div className="container-fluid hvr-grow-shadow m-0 p-0">
        <nav className="navbar navbar-expand-xl navbar-dark m-30 p-10 clearfix" id="nav-colors">
        <button className="navbar-toggler" type="button" data-toggle="collapse"  data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto" style={{position:"relative", zIndex:'99'}}>
                <li className="nav-item active">
                    <Link className="nav-link hvr-glow" to="/"><i className="fas fa-tree border border-success rounded-circle" style={{color: 'darkgreen'}} /></Link>
                </li>
            <li className="nav-item active">
                <Link className="nav-link hvr-glow" to="/"> 
                About Us <span className="sr-only">(current)</span></Link>
            </li>
            <br/>
            <li className="nav-item dropdown">
                <Link className="nav-link hvr-glow dropdown-toggle" 
                href="#" 
                id="navbarDropdown" 
                role="button" 
                data-toggle="dropdown" 
                aria-haspopup="true" 
                aria-expanded="true">
                Services
                <span className="sr-only">(current)</span>
                </Link>
                <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                <a className="dropdown-item" href="#">Surveys</a>
                <div className="dropdown-divider"></div>
                <a className="dropdown-item" href="#">Tree Plantations</a>
                <div className="dropdown-divider"></div>
                <a className="dropdown-item" href="#">Inspection</a>
                </div>
                
            </li>
            <li className="nav-item active">
                <Link className="nav-link hvr-glow" to="/api/blog/bloglist/"> 
                Blog <span className="sr-only">(current)</span></Link>
            </li>
            <li className="nav-item">
                <a className="nav-link hvr-glow" href="#">Contact Us</a>
            </li>
            {isAuthenticated ?  authLinks() : guestLinks()}
            </ul>
            <form className="form-inline my-2 my-lg-0">
            <input className="form-control mr-md-2" type="search" placeholder="Search" aria-label="Search"></input>
            <button className="hvr-glow btn btn-outline-success xs" type="submit">Search</button>
            </form>
            
        </div>
        </nav>
        </div>
    </>
)
};


const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});
export default connect(mapStateToProps, { logout })(Navbar);