import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from '../actions/auth';
import '../assets/login.css'

const Login = ({ login, isAuthenticated }) => {
    const [formData, setFormData] = useState({
        email: '',
        password: '' 
    });

    const { email, password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        login(email, password);
    };


    // if user authenticated
    // Redirect to Home page
    if(isAuthenticated){
        return <Redirect to='/' />
    }

    return(
        <div className="mx-5 my-5">
        <div className='card container col-lg-4 col-md-4 col-sm-12 p-4 shadow-lg bg-white' style={{borderRadius:'17px'}}>
            <div className="container text-center">
                <i className="fas fa-tree fa-3x border border-success rounded-circle p-5 hvr-grow-shadow" style={{color: 'darkgreen'}} />
            </div>
            <br/>
            <form className="card-body container" method="POST" onSubmit={e => onSubmit(e)}>
                <div className='form-group'>
                    <label htmlfor='email'>Email</label>
                    <input className="form-control" 
                    placeholder='Your Email ID' 
                    type='email'
                    name='email'
                    value={email}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <br/>
                <div className='form-group'>
                    <label htmlfor='password'>Password</label>
                    <input type='password' 
                    className='form-control' 
                    placeholder='Your password' 
                    name='password'
                    value={password}
                    onChange={e => onChange(e)}
                    minLength='6'
                    required
                    />
                </div>
                <br/>
                <div className="text-right">
                    <button type='submit' className='ml-auto btn btn-success' title='Login to SafeTrees'>LOGIN</button>
                </div>
                <br/>
            </form>
            <hr style={{ width:"50%", margin:'0 auto', backgroundColor:'green' }}/>
            <div className='text-center p-3'>
                <small>Forgot Password? <Link to='/reset-password'>Reset Password</Link></small>
            </div>
            <div className='text-center p-3'>
                <small>Don't have an account? <Link to='/signup'>Sign up!</Link></small>
            </div>
        </div>
        </div>
    );

};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { login })(Login);