import React from 'react';
import { Link } from 'react-router-dom';
import '../assets/home.css';

const Home = () => (
<div className="mx-5 my-5">
    <div className="blog-card  shadow-lg background-color: honeydew"
    style={{borderRadius:'17px'}}>
        <div className="title-content">
            <h3><Link href="#">Welcome to SafeTree First</Link></h3>
        </div>
        <div className="card-info">
        <p>If you are an employee and/or Administator, Please login using the Login button!</p>
        <Link style={{outline:'none', border:'none', borderRadius:'4px', color:'white', backgroundColor:'darkgreen'}} to="/login">Login</Link>
        
        <p>If you're just here to visit and explore on Environment awareness programmes, surveys, click the following button</p>
        <Link style={{outline:'none', border:'none', borderRadius:'4px', color:'white', backgroundColor:'darkgreen'}} to="/api/blog">Blog Posts</Link>
        </div>
        <div className="gradient-overlay"></div>
        <div className="color-overlay"></div>
    </div>
</div>
        );

export default Home;