import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { surveyform } from '../actions/auth';
import '../assets/home.css';

const Surveyform = ({ surveyform }) => {
    const [formData, setFormData] = useState({
        areaName: '',
        treeName: '',
        treeAge: '',
        treeImage: '',
        treeHealth: '',
        treeGirth: '',
        treeHeight: '',
        treeLocation: '',
        surveyDate: '',
    });

    const { areaName, treeName, treeAge, treeImage, treeHealth, treeGirth, treeHeight, treeLocation, surveyDate } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        surveyform(areaName, treeName, treeAge, treeImage, treeHealth, treeGirth, treeHeight, treeLocation, surveyDate);
    };

return(
    
    <>
    <div className="mx-5 my-5">
        <div className='card container col-lg-4 col-md-4 col-sm-12 my-5 p-4 shadow-lg bg-white' style={{borderRadius:'17px'}}>
            <div className="container text-center">
                <i className="fas fa-tree fa-3x border border-success rounded-circle p-5 hvr-grow-shadow" style={{color: 'darkgreen'}} />
            </div>
            <br/>
            <form method="post" action="post"
            className="card-body container" method="POST" onSubmit={e => onSubmit(e)}>
                <div className='form-group'>
                    <label htmlFor='areaName'>Area Name</label>
                    <input className="form-control" 
                    placeholder='Area Name' 
                    type='text'
                    name='areaName'
                    value={areaName}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <br/>
                <div className='form-group'>
                    <label htmlFor='treeName'>Tree Name</label>
                    <input type='text' 
                    className='form-control' 
                    placeholder='Tree Name' 
                    name='treeName'
                    value={treeName}
                    onChange={e => onChange(e)}
                    minLength='6'
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='treeAge'>Tree Age</label>
                    <input className="form-control" 
                    placeholder='Tree Age' 
                    type='text'
                    name='treeAge'
                    value={treeAge}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='treeImage'>Iree Image</label>
                    <input className="form-control" 
                    placeholder='Tree Image' 
                    type='file'
                    name='treeImage'
                    value={treeImage}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='treeHealth'>Tree Health</label>
                    <input className="form-control" 
                    placeholder='Tree Health' 
                    type='text'
                    name='treeHealth'
                    value={treeHealth}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='treeGirth'>Tree Girth</label>
                    <input className="form-control" 
                    placeholder='Tree Girth' 
                    type='text'
                    name='treeGirth'
                    value={treeGirth}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='treeHeight'>Tree Location</label>
                    <input className="form-control" 
                    placeholder='Tree Height' 
                    type='text'
                    name='treeHeight'
                    value={treeHeight}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='treeLocation'>Tree Location</label>
                    <input className="form-control" 
                    placeholder='Tree Location' 
                    type='text'
                    name='treeLocation'
                    value={treeLocation}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <div className='form-group'>
                    <label htmlFor='surveyDate'>Survey Date</label>
                    <input className="form-control" 
                    placeholder='Survey Date' 
                    type='text'
                    name='surveyDate'
                    value={surveyDate}
                    onChange={e => onChange(e)}
                    required
                    />
                </div>
                <br/>
                <div className="text-right">
                    <button type='submit' className='ml-auto btn btn-success' title='Login to SafeTrees'>Submit</button>
                </div>
                <br/>
            </form>
        </div>
        </div>
    </>
    );
};

const mapStateToProps = state => ({
});

export default connect(null, { surveyform })(Surveyform);