import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';


import Home from './containers/Home';
import Login from './containers/Login';
import Signup from './containers/Signup';
import Activate from './containers/Activate';
import ResetPassword from './containers/ResetPassword';
import ResetPasswordConfirm from './containers/ResetPasswordConfirm';
import TestingContainer from './containers/TestingContainer';
import BlogContact from './components/BlogContact';
import Layout from './hocs/Layout';
import Blog from './components/Blog';
import BlogDetail from './components/BlogDetail';
import Category from './components/Category';
import surveyform from './components/Surveyform';

import { Provider, connect } from 'react-redux';
import store from './store';


const App = () => (
    <Provider store={store}>
        <Router>
            <Layout>
                <Switch>
                    <Route exact path = "/" component={Home} />
                    <Route exact path = "/login" component={Login} />
                    <Route exact path = "/signup" component={Signup} />
                    <Route exact path = "/reset-password" component={ResetPassword} />
                    <Route exact path = "/password/reset/confirm/:uid/:token" component={ResetPasswordConfirm} />
                    <Route exact path = "/activate/:uid/:token" component={Activate} />
                    <Route exact path = "/api/blog" component={Blog} />
                    <Route exact path = "/api/blog/:id" component={BlogDetail} />
                    <Route exact path = "/api/blog/contact" component={BlogContact} />
                    <Route exact path = "/api/category/:id" component={Category} />
                    <Route exact path = "/api/survey/treeSurvey/" component={surveyform} />
                    
                </Switch>
            </Layout>
        </Router>
    </Provider>
);

export default App;